<template>
  <label class="switch">
    <input :id="idname" type="checkbox" @click="testclick" />
    <span class="slider round"></span>
  </label>
</template>

<script>
export default {
  name: "Button",
  props: {
    bool: Boolean,
    idname: String
  },
  methods: {
    testclick() {
      this.$emit("test-click");
      let test = document.getElementById(this.idname);
      if (this.bool) {
        test.checked = true;
      } else {
        test.checked = false;
      }
    }
  },
  watch: {
    bool: function(bool) {
      let test = document.getElementById(this.idname);
      if (bool) {
        test.checked = true;
      } else {
        test.checked = false;
      }
    }
  }
};
</script>

<style>
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: rgb(54, 202, 216);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgb(54, 202, 216);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
</style>
